<template>
	<view class="box" v-if="good_detail.promptPrice">
		<view class="banner">
			<swiper class="goods-box" :indicator-dots="true" :autoplay="false">
				<template v-for="(item,k) in good_detail.o2oChgPicList">
					<swiper-item class="swiper-item" :key="k">
						<image lazy-load class="img" :src="item.url" mode="aspectFill" :data-index="k" @tap="previewPhoto"></image>
					</swiper-item>
				</template>
			</swiper>
			<view class="banner-title">
				<view class="top">
					<view class="name font-bold">{{good_detail.spuTitle}}</view>
					<view class="reward">{{good_detail.jfValue | formatNum}}活动积分可得
					 	<view class="border-btn">送{{good_detail.o2oMarketingRetCoupon.couponName}}</view>
					</view>
				</view>
				<view class="title">
					<text class="text">
						{{good_detail.spuTitle2}}
					</text>
					<button open-type="share" class="btn-share share"><view class="iconfont iconfenxiang"></view>
						分享</button>
					<!-- <view class="share">
						<view class="iconfont iconfenxiang"></view>
						分享
					</view> -->
				</view>
			</view>
		</view>
		<titleLine :title-list="[{title:'产品详情',id:1}]"></titleLine>
		<jyf-parser class="detail-box" :html="good_detail.o2oBigText.content" lazy-load ref="article" selectable
		 show-with-animation use-anchor>加载中...</jyf-parser>
		
		<titleLine v-if="good_list.length>0" :title-list="[{title:'卡片推荐',id:1}]"></titleLine>
		<view class="card-recommend" v-if="good_list.length>0" >
			<template v-for="(item,k) in good_list">
				<navigator class="card-li" :key="k" :url="'/pages/goods/activity_detail?seqId='+item.seqId+'&merchantShopId='+item.merchantShopId">
					<image lazy-load class="img" :src="item.mainPicUrl" mode="aspectFill"></image>
					<view class="right">
						<view class="title">
							{{item.spuTitle2}}
							
						</view>
						<view class="desc">
							{{item.spuDesc}}
						</view>
						<view class="price">
							{{item.spuTitle}}
						</view>
					</view>
				</navigator>
			</template>
			<navigator url="/pages/index/area_activity" open-type="navigateBack" class="primary-btn">
				更多
				<image lazy-load class="img" src="../../static/images/more.png" mode="aspectFill"></image>
			</navigator>

		</view>
		<view class="blank"></view>
		<view class="fix-box">
			<view class="bg-btn" @tap="introOrder">立即兑换</view>
			<!-- #ifdef MP -->
			<button v-if="!is_login" class="confirm-btn" open-type="getUserInfo" @getuserinfo="wxLogin">微信授权登录</button>
			<!-- #endif -->
		</view>
		<add-cart @specChoose='chooseType' @cartClose="closeCart" v-if="cart_show" :showType="pup_type" :specList.sync="good_detail.o2oSpuSpecList"
		 :spuSpecSkuMap.sync="good_detail.o2oSpuSpecSkuMap" :goodDetail.sync="good_detail"></add-cart>
	</view>
</template>
<script>
	import addCart from './components/add-cart.vue'
	import titleLine from '@/components/title-line.vue'
	import jwt_decode from "jwt-decode";
	import {mapState} from 'vuex'
	import {
		getProvider,
		getCode
	} from '@/common/js/login/newlogin.js'
	let provider="";
	import Vue from 'vue'
	let seqId = "";
	let merchantShopId = ""
	export default {
		data() {
			return {
				is_login:false,
				cart_show: false,
				pup_type: "shopcar", //弹窗类型
				good_detail: {
					o2oBigText: {
						content: ""
					},
					o2oChgPicList: [],
					o2oSpuSpecList: [],
					o2oSpuSpecSkuMap: [],
					o2oMarketingRetCoupon: {
						couponName: ""
					}
				},
				good_list:[]
			}
		},
		components: {
			titleLine,addCart

		},
		computed:{
			...mapState({
				user:state=>state.user
			})
		},
		onLoad(option) {
			seqId = option.seqId;
			merchantShopId = option.merchantShopId;
			this.loadGoodDetail();
			let local_token=this.$common.getLocalSync('token');
			this.is_login=local_token?true:false;
			this.loadGood()
			uni.showLoading({
				title: "加载中"
			})
			setTimeout(function() {
				uni.hideLoading()
			}, 500)
		},
		onShow() {
			this.cart_show = false;
			// #ifdef MP
			(async () => {
				//获取服务商信息
				provider = await getProvider();
				//获取code
				this.code = await getCode(provider[0])
			})()
			// #endif
		},
		// #ifdef MP
		onShareAppMessage(res) {
			return {
				title: this.good_detail.spuTitle,
				path: '/pages/goods/activity_detail?seqId='+seqId+'&merchantShopId='+merchantShopId+'&shareCode='+this.user.shareCode
			}
		},
		// #endif
		methods: {
			chooseType(e) {
				let key = e.key;
				let index = e.index;
				this.good_detail.o2oSpuSpecList[key].spec_choose = index
			},
			loadGoodDetail() {
				this.$api.get('/o2oSpu/getAllInfoByPkAndshopSeqId', {
					params: {
						seqId: seqId,
						merchantShopId: merchantShopId
					}
				}).then(res => {
					if (res.code == 200) {
						let data = res.data

						for (let item of data.o2oSpuSpecList) {
							// 默认选中第一个
							Vue.set(item, 'spec_choose', 0)
						}
						if (data.spuLabel) {
							// 转成数组
							data.spuLabel = data.spuLabel.split(',')
						}
						this.good_detail = data;
					}
				})
			},
			loadGood(){
				this.$api.get('/o2oSpu/findHeadPage', {
					params: {
						pageno: 1,
						pagesize: 3,
						promptType: 'RET_COUPON',//促销类型
						purchaseType: 'JF', //购买类型
						onSale: 'SALE'
					}
				}).then((res) => {
					if (res.code && res.code != 200) {
						this.good_list=[]
						return
					}
					let data = res;
					// 接口返回的当前页数据列表 (数组)
					let curPageData = data.list;
					this.good_list=curPageData
				})
			},
			introOrder() {
				this.pup_type = 'buy';
				this.cart_show = true
			},
			intoCart() {
				this.pup_type = 'shopcar'
				this.cart_show = true
			},
			closeCart() {
				this.cart_show = false
			},
			// 微信登录
			wxLogin(e) {
				// #ifndef H5
				let that = this;
				if (!this.code) {
					uni.showToast({
						icon: 'none',
						title: '正在加载中，稍等一下',
						duration: 1000
					});
				}
				if (e.detail && e.detail.errMsg == 'getUserInfo:ok') {
					uni.getUserInfo({
						success(info_res) {
							that.info_res = info_res;
							that.sendLogin()
						}
					})
				} 
				// #endif
			},
			sendLogin() {
				this.$api.post('/login/save', {
					code: this.code,
					rawData: this.info_res.rawData,
					iv: this.info_res.iv,
					signature: this.info_res.signature,
					encryptedData: this.info_res.encryptedData
				}).then(res => {
					if (res.code == 200) {
						let token = res.data;
						let user_json =jwt_decode(token);
						this.$common.setLocal('token', res.data);
						this.$common.setLocal('customerSeqId', user_json.userId);
						this.$common.setLocal('customerName', user_json.userName || "");
						this.$store.commit('saveUser',user_json)
						this.is_login=true;
					}
				})
			},
			previewPhoto(e) {
				let index = e.currentTarget.dataset.index;
				let list = this.good_detail.o2oChgPicList.map((item, index) => {
					return item.url;
				});
				uni.previewImage({
					current: list[index],
					urls: list,
					longPressActions: {
						itemList: ['保存图片'],
						success: function(res) {
							uni.getImageInfo({
								src: list[res.index],
								success: function(image) {
									uni.saveImageToPhotosAlbum({
										filePath: image.path,
										success: function() {
											uni.showToast({
												title: '图片保存成功',
												icon: 'none',
												duration: 3000
											});
										}
									});
								}
							});
						}
					}
				});
			},
		}
	}
</script>
<style lang="scss">
	.border-btn {
		padding: 6rpx 20rpx;
		border:1px solid rgba(255,209,205,1);
		border-radius:22rpx;
		color: #ED6458;
		font-size: 22rpx;
		margin-left: 30rpx;
		font-weight: 500;
	}
	.confirm-btn {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: red;
		z-index: 10;
		opacity: 0;
	}
	
	.scale>div,
	.scale>img {
		height: 0;
		padding-bottom: 45%;
		overflow: hidden;
		background-position: center center !important;
		background-repeat: no-repeat !important;
		background-size: cover !important;
	}

	.primary-btn {
		width: 574rpx;
		height: 64rpx;
		background: rgba(255, 158, 151, 1);
		border-radius: 32rpx;
		padding: 0;
		text-align: center;
		line-height: 64rpx;
		color: white;
		margin: 40rpx auto;
		font-size: 24rpx;

		.img {
			width: 32rpx;
			height: 21rpx;
			margin-left: 16rpx;
		}
	}

	.bg-btn {
		width: 100%;
	}

	.box {
		width: 682rpx;
		margin: 0 auto;

		.banner {
			background: white;
			width: 100%;
			height: auto;
			border-radius: 14rpx;
			overflow: hidden;
			margin-top: 30rpx;

			.goods-box {
				width: 100%;
				height: 310rpx;

				.img {
					width: 100%;
					height: 100%;
				}
			}

			.banner-title {
				font-size: 30rpx;
				padding: 30rpx 20rpx;

				.top {
					display: flex;
					align-items: center;
					margin-bottom: 10rpx;

					.name {
						font-size: 32rpx;
						color: #FF9E97;
						margin-right: 18rpx;
					}

					.reward {
						font-size: 20rpx;
						color: #444444;
						display: flex;
						align-items: center;
					}

				}

				.title {
					display: flex;
					justify-content: space-between;
					align-items: center;
					flex-grow: 1;

					.text {
						color: #333;
						margin-top: 10rpx;
					}
				}

				.share {
					display: flex;
					background: linear-gradient(90deg, rgba(250, 98, 85, 1) 0%, rgba(252, 183, 176, 1) 100%);
					border-radius: 0px 20rpx 0px 20rpx;
					color: white;
					font-size: 20rpx;
					padding: 4rpx 10rpx;
					flex-shrink: 0;
					margin-left: 0;
					margin-right: 0;
					line-height: 1.5;
					.iconfont {
						font-size: 20rpx;
						margin-right: 4rpx;
					}

				}
			}
		}

		.detail-box {
			width: calc(100% - 40rpx);
			height: auto;
			border-radius: 14rpx;
			overflow: hidden;
			background: white;
			font-size: 30rpx;
			padding: 20rpx;

			image {
				width: 100%;
			}
		}

		,
		.card-recommend {
			height: auto;

			.card-li {
				width: calc(100% - 60rpx);
				padding: 30rpx;
				background: white;
				display: flex;
				margin: 14rpx 0;

				.img {
					width: 214rpx;
					height: 140rpx;
					flex-shrink: 0;
					margin-right: 30rpx;
				}

				.right {
					width: calc(100% - 230rpx);
					flex-grow: 1;
					font-size: 30rpx;
					display: flex;
					flex-wrap: wrap;
					align-content: space-between;

					.title {
						width: 100%;
						overflow: hidden;
						text-overflow: ellipsis;
						white-space: nowrap;
					}

					.desc {
						width: 100%;
						font-size: 22rpx;
						color: #444444;
					}

					.price {
						width: 100%;
						color: #FF9E97;
					}
				}
			}
		}

	}
</style>
